import React, { PureComponent } from 'react';
import { Bar, BarChart, CartesianGrid, Cell, Legend, ResponsiveContainer, Tooltip, XAxis, YAxis } from 'recharts';
import { connect } from 'react-redux';
import { loadMssqlEnvironmentUsageThunk } from '../../../store/thunks';
import {
  LICENSABLE_FEATURE_DEFAULT_COLOR,
  LICENSABLE_FEATURE_DEFAULT_SECONDARY_COLOR,
  LICENSABLE_FEATURES_COLORS
} from '../../LicensableFeaturesGraph/LicensableFeaturesConstants';
import { Center, LightTooltip } from '../../../../shared/components';
import { Loader } from '../../../../shared/components/loaders';
import { FullScreenIcon } from '../../FullScreenIcon/FullScreenIcon';
import classnames from 'classnames';

import './EnvironmentUsageTile.local.scss';

export const EnvironmentUsageChartTooltip = (data) => {
  if (!data || !data.payload || data.payload.length < 3) {
    return '';
  }

  const coresValue = data.payload[0].value;
  const maxCoresValue = data.payload[0]?.payload.maxCores;
  const devicesCalValue = data.payload[1].value;
  const usersCalValue = data.payload[2].value;
  return (
    <div className="exec-dashboard-chart-tooltip">
      <LightTooltip placement="bottom-end">
        <div className="exec-dashboard-chart-tooltip-content">
          <div className="exec-dashboard-chart-tooltip-row">
            <div className="exec-dashboard-chart-tooltip-name">Cores</div>
            <div className="exec-dashboard-chart-tooltip-value">{coresValue} (max {maxCoresValue})</div>
          </div>
          <div className="exec-dashboard-chart-tooltip-row">
            <div className="exec-dashboard-chart-tooltip-name">Devices CAL</div>
            <div className="exec-dashboard-chart-tooltip-value">{devicesCalValue}</div>
          </div>
          <div className="exec-dashboard-chart-tooltip-row">
            <div className="exec-dashboard-chart-tooltip-name">Users CAL</div>
            <div className="exec-dashboard-chart-tooltip-value">{usersCalValue}</div>
          </div>
        </div>
      </LightTooltip>
    </div>
  );
};

class EnvironmentUsageTile extends PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      fullScreen: false,
    };
  }

  componentDidMount() {
    const { getEnvironmentUsage } = this.props;
    getEnvironmentUsage();
  }

  UNSAFE_componentWillReceiveProps(newProps) {
    if (newProps.selectedServer !== this.props.selectedServer) {
      const { getEnvironmentUsage } = this.props;
      setTimeout(() => getEnvironmentUsage(newProps.selectedServer));
    }
  }

  render() {
    const { environmentUsage, selectedServer, isLoading } = this.props;
    if (isLoading) {
      return (
        <>
          <div className="exec-dashboard-graph-container mssql-environment-usage-graph-container">
            <Center>
              <Loader>Loading Data...</Loader>
            </Center>
          </div>
        </>
      );
    }

    const chartData = this.mapData(environmentUsage);
    const { fullScreen } = this.state;
    return (
      <div className={classnames('exec-dashboard-graph-container mssql-environment-usage-graph-container', { 'show-full-screen': fullScreen })}>
        <FullScreenIcon fullScreen={fullScreen} fullScreenToggle={() => this.setState({ fullScreen: !fullScreen })} />

        {chartData.length ? (
          <>
            <div className="chart-title">Cores / CALs Usage</div>
            <ResponsiveContainer height={fullScreen ? 700 : 380}>
              <BarChart data={chartData} barGap={0} margin={{ left: 20 }}>
                <CartesianGrid strokeDasharray="3 3" vertical={false} />
                <XAxis height={50} dataKey="label" tickSize={false} axisLine={false} tick={this.renderCustomXAxisTick} />
                <YAxis
                  width={10}
                  tickSize={false}
                  allowDecimals={false}
                  type="number"
                  domain={[0, selectedServer ? 2 : 'dataMax']}
                  stroke="rgba(4, 17, 37, 0.87)"
                />
                <Bar dataKey="cores" fill="#13369f">
                  {chartData.map((entry, index) => {
                    return <Cell key={`${entry.name}-${index}`} fill="#13369f" />;
                  })}
                </Bar>
                <Bar dataKey="devicesCal" fill="#f2c94c">
                  {chartData.map((entry, index) => {
                    return <Cell key={`${entry.name}-${index}`} fill="#f2c94c" />;
                  })}
                </Bar>
                <Bar dataKey="usersCal" fill="#55c667">
                  {chartData.map((entry, index) => {
                    return <Cell key={`${entry.name}-${index}`} fill="#55c667" />;
                  })}
                </Bar>
                <Tooltip content={EnvironmentUsageChartTooltip} cursor={false} />
                <Legend />
              </BarChart>
            </ResponsiveContainer>
          </>
        ) : (
          <Center>
            No Data Available
          </Center>
        )}
      </div>
    );
  }

  renderCustomXAxisTick = ({ x, y, payload }) => {
    const licenseConfig = LICENSABLE_FEATURES_COLORS.find((config) => config.type === payload.value);
    return (
      <g transform={`translate(${x},${y})`}>
        <text className="x-axis-label" x={0} y={0} dy={20} textAnchor="middle" fill={licenseConfig?.color || '#aaa'}>
          {payload.value}
        </text>
      </g>
    );
  };

  mapData = (environmentUsage) => {
    return (environmentUsage || []).map((env) => {
      const licenseConfig = LICENSABLE_FEATURES_COLORS.find((config) => env.abbreviation === config.type);
      return {
        ...env,
        name: env.servername,
        label: env.servername,
        devicesCal: env.estimate_calcount_fordevices,
        usersCal: env.estimate_calcount_forusers,
        maxCores: env.max_core,
        color: licenseConfig ? licenseConfig.color : LICENSABLE_FEATURE_DEFAULT_COLOR,
        secondaryColor: licenseConfig ? licenseConfig.secondaryColor : LICENSABLE_FEATURE_DEFAULT_SECONDARY_COLOR,
      };
    });
  };
}

export const MssqlConnectedEnvironmentUsageTile = connect(
  state => {
    return {
      isLoading: state.executiveDashboard.flags.mssqlEnvironmentUsage.loading,
      environmentUsage: state.executiveDashboard.mssqlEnvironmentUsage,
    };
  },
  dispatch => ({
    getEnvironmentUsage: (serverId) => dispatch(loadMssqlEnvironmentUsageThunk(serverId)),
  })
)(EnvironmentUsageTile);
