import React from 'react';
import { connect } from 'react-redux';
import { loadLicenseAllocationThunk } from '../../store/thunks';
import { getLicensesData, licensesLoading } from '../../store/selectors';
import { Center, LightTooltip, Table } from 'shared/components';
import CircularProgress from '@material-ui/core/CircularProgress';
import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import { mdiPencil } from '@mdi/js';
import { AssignLicenseModal } from '../assign-license-modal/AssignLicenseModal';

import './AllocatedLicensesTable.local.scss';

const columns = [
  { label: '' },
  { label: 'Product' },
  { label: 'Server' },
  { label: 'Metric' },
  { label: 'Term' },
  { label: 'Type' },
  { label: 'Assigned' },
  { label: 'Available' },
  { label: '' },
];

const CellWithTooltip = ({ name }) => {
  if (name && name.length > 16) {
    return (
      <LightTooltip placement="bottom" title={<span>{name}</span>}>
        <span className="tooltip-cell ellipsis">{name}</span>
      </LightTooltip>
    );
  }
  return <span className="tooltip-cell">{name}</span>;
};

/* eslint-disable jsx-a11y/click-events-have-key-events */
const rowRenderer = (row, index, isParentRow, hasChildren, openLicenseAssignments, expandedProducts, toggleGroup) => (
  <Table.Row hover key={`${row.license_id}_${index}`} className="statement-row">
    <Table.Cell>
      {isParentRow && hasChildren && (
        <span tabIndex={0} role="button" className="license-table-expandable-icon" onClick={() => toggleGroup(row.product_name, expandedProducts)}>
          {expandedProducts[row.product_name] ? '-' : '+'}
        </span>
      )}
    </Table.Cell>
    <Table.Cell>
      {isParentRow && (
        <CellWithTooltip name={row.product_name} />
      )}
    </Table.Cell>
    <Table.Cell>
      <span className="license-table-cell">{row.host_name}</span>
    </Table.Cell>
    <Table.Cell>
      <span className="license-table-cell">{isParentRow ? row.license_metric : ''}</span>
    </Table.Cell>
    <Table.Cell>
      <span className="license-table-cell">{isParentRow ? row.license_term : ''}</span>
    </Table.Cell>
    <Table.Cell>
      <span className="license-table-cell">{isParentRow ? row.license_type : ''}</span>
    </Table.Cell>
    <Table.Cell>
      <span className="license-table-cell">{row.licenses_assigned}</span>
    </Table.Cell>
    <Table.Cell>
      <span className="license-table-cell">{isParentRow ? row.licenses_available : ''}</span>
    </Table.Cell>
    <Table.Cell>
      <span className="license-target-cell action-cell">
        <LightTooltip placement="top" title="Edit assignment">
          <SvgIcon onClick={() => openLicenseAssignments(row, isParentRow)}>
            <Icon path={mdiPencil} />
          </SvgIcon>
        </LightTooltip>
      </span>
    </Table.Cell>
  </Table.Row>
);

const TableRenderer = ({ isLoading, licenses, noDataMessage, tableProps, openLicenseAssignments, expandedProducts, toggleGroup }) => {
  if (isLoading) {
    return (
      <Center>
        <CircularProgress />
      </Center>
    );
  }
  if (licenses.length === 0) {
    return <Center>{noDataMessage}</Center>;
  }
  return <Table {...tableProps}>
            {licenses.map((row, index) => {
              const hasChildren = row?.children?.length;
              return (
                <>
                  {rowRenderer(row, index, true, hasChildren, openLicenseAssignments, expandedProducts, toggleGroup)}
                  {hasChildren && expandedProducts[row.product_name] && (
                    row.children.map((child, childIndex) =>
                      rowRenderer(child, `${index}_${childIndex}`, false, false, openLicenseAssignments, expandedProducts, toggleGroup))
                  )}
                </>
              );
            })}
         </Table>;
};

class AllocatedLicensesTable extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      sortBehavior: { sortKey: 'product_name', order: 'desc' },
      assignLicenseModalOpened: false,
      selectedAssignment: null,
      expandedProducts: {},
      editableServer: false
    };
  }

  componentDidMount() {
    this.props.getLicenses();
  }

  render() {
    const { licensesData, isLoading } = this.props;
    const { sortBehavior, assignLicenseModalOpened, selectedAssignment, expandedProducts, editableServer } = this.state;
    const tableProps = { columns, sortBehavior, changeSort: this.changeSort };
    const groupedLicenses = this.groupLicenses(licensesData);
    const assignLicenseModalActions = {
      open: (assignmentToEdit, canModifyServer) => {
        this.setState({ assignLicenseModalOpened: true, selectedAssignment: assignmentToEdit, editableServer: canModifyServer });
      },
      close: () => {
        this.setState({ assignLicenseModalOpened: false, selectedAssignment: null, editableServer: false });
      }
    };

    return (
      <>
        <TableRenderer
          isLoading={isLoading}
          licenses={groupedLicenses}
          tableProps={tableProps}
          openLicenseAssignments={assignLicenseModalActions.open}
          expandedProducts={expandedProducts}
          toggleGroup={this.toggleGroup}
          noDataMessage="No License Data Available"
        />

        <AssignLicenseModal
          modalActions={assignLicenseModalActions}
          open={assignLicenseModalOpened}
          selectedAssignment={selectedAssignment}
          editableServer={editableServer}
        />
      </>
    );
  }

  changeSort = (sortKey, order) => () => {
    this.setState({ sortBehavior: { sortKey, order } });
  };

  toggleGroup = (productName, expandedProducts) => {
    if (expandedProducts[productName]) {
      delete expandedProducts[productName];
    } else {
      expandedProducts[productName] = true;
    }
    this.setState({ expandedProducts });
    this.forceUpdate();
  };

  groupLicenses = (licenses) => {
    return Object.values(licenses.reduce((acc, current) => {
      // const groupKey = `${current.product_name}_${current.license_metric}`;
      const groupKey = `${current.license_id}`;
      if (acc[groupKey]) {
        acc[groupKey].children.push(current);
        acc[groupKey].licenses_assigned += current.licenses_assigned;
      } else {
        const topLevelItem = { ...current, children: [current] };
        delete topLevelItem.server_id;
        delete topLevelItem.host_name;
        acc[groupKey] = topLevelItem;
      }
      return acc;
    }, {})).map((license) => {
      if (license.licenses_assigned === 0) {
        delete license.children;
      }
      return license;
    });
  };
}

export const ConnectedAllocatedLicensesTable = connect(
  state => ({
    isLoading: licensesLoading(state),
    licensesData: getLicensesData(state)
  }),
  dispatch => ({
    getLicenses: () => dispatch(loadLicenseAllocationThunk())
  })
)(AllocatedLicensesTable);
