import React from 'react';
import classnames from 'classnames';
import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import { mdiAlert } from '@mdi/js';
import { LightTooltip } from 'shared/components';

import './OptionItem.local.scss';

export const OptionItem = ({ option }) => {
  return (
    <div className={classnames('option', { 'has-issues': option.hasIssues })}>
      <div className="value" style={{ backgroundColor: option.color }} />
      {option.tooltip ? (
        <LightTooltip placement="right-start" title={option.tooltip}>
          <div className="label">{option.label}</div>
        </LightTooltip>
      ) : (
        <div className="label">{option.label}</div>
      )}
      {option.hasIssues && (
        <SvgIcon className="alert-icon">
          <Icon path={mdiAlert} />
        </SvgIcon>
      )}
    </div>
  );
};
