import React from 'react';
import { Button } from 'shared/components/controls';

export function ToggleSelection({ hasSelectedServer, deselectServer }) {
  if (hasSelectedServer) {
    return (
      <Button style={{ color: '#03A9F4' }} onClick={() => deselectServer()}>
        Deselect
      </Button>
    );
  }
  return '';
}
