import { createReducer, types } from 'shared/lib/redux-utils';

import {
  ADD_USER_TO_GROUPS,
  CREATE_USER,
  DEACTIVATE_USER,
  GRANT_PRIVILEGE,
  LOAD_ALL_USERS,
  LOAD_USER,
  REMOVE_USER_FROM_GROUPS,
  REVOKE_PRIVILEGE,
  SELECT_USER,
  UPDATE_GROUP_CONNECTIONS_LIMIT,
  UPDATE_USER_EXPIRATION
} from '../actions';

import { mapLoadedUser, mapUsersData } from '../mappers';

export const initialState = {
  users: {
    all: [],
    selected: null
  },
  flags: {
    users: { loading: false },
    user: { loading: false, deactivating: false, updateExpiration: false },
    privileges: { disabled: false },
    group: { updateConnectionsLimit: false }
  },
  debug: {
    user: {},
    groups_update: {},
    privileges: {}
  }
};

export const producers = {
  [types.loading(LOAD_ALL_USERS)]: draft => {
    draft.flags.users.loading = true;
  },
  [types.success(LOAD_ALL_USERS)]: (draft, { payload }) => {
    draft.users.all = mapUsersData(payload);

    draft.flags.users.loading = false;
  },
  [types.action(SELECT_USER)]: (draft, { payload }) => {
    draft.users.selected = payload;
  },
  [types.loading(LOAD_USER)]: draft => {
    draft.flags.user.loading = true;
    draft.users.selected = null;
  },
  [types.success(LOAD_USER)]: (draft, { payload }) => {
    draft.flags.user.loading = false;
    draft.users.selected = mapLoadedUser(payload);
  },
  [types.loading(DEACTIVATE_USER)]: draft => {
    draft.flags.user.deactivating = true;
  },
  [types.success(DEACTIVATE_USER)]: (draft, { payload }) => {
    draft.flags.user.deactivating = false;
    draft.users.all = mapUsersData(payload);
  },
  [types.fail(DEACTIVATE_USER)]: (draft, { payload }) => {
    draft.flags.user.deactivating = false;
    draft.debug.user.errors = payload;
  },
  [types.loading(GRANT_PRIVILEGE)]: draft => {
    draft.flags.privileges.disabled = true;
  },
  [types.success(GRANT_PRIVILEGE)]: (draft, { payload }) => {
    draft.flags.privileges.disabled = false;
    draft.users.selected = mapLoadedUser(payload);
  },
  [types.fail(GRANT_PRIVILEGE)]: (draft, { payload }) => {
    draft.flags.privileges.disabled = false;
    draft.debug.privileges.errors = payload;
  },
  [types.loading(REVOKE_PRIVILEGE)]: draft => {
    draft.flags.privileges.disabled = true;
  },
  [types.success(REVOKE_PRIVILEGE)]: (draft, { payload }) => {
    draft.flags.privileges.disabled = false;
    draft.users.selected = mapLoadedUser(payload);
  },
  [types.fail(REVOKE_PRIVILEGE)]: (draft, { payload }) => {
    draft.flags.privileges.disabled = false;
    draft.debug.privileges.errors = payload;
  },
  [types.action(ADD_USER_TO_GROUPS)]: draft => {
    draft.flags.user.updating_groups = true;
  },
  [types.loading(ADD_USER_TO_GROUPS)]: draft => {
    draft.flags.user.updating_groups = true;
  },
  [types.success(ADD_USER_TO_GROUPS)]: (draft, { payload }) => {
    draft.flags.user.updating_groups = false;
    draft.users.selected = mapLoadedUser(payload);
  },
  [types.fail(ADD_USER_TO_GROUPS)]: (draft, { payload }) => {
    draft.flags.user.updating_groups = false;
    draft.debug.groups_update.errors = payload;
  },
  [types.loading(REMOVE_USER_FROM_GROUPS)]: draft => {
    draft.flags.user.updating_groups = true;
  },
  [types.success(REMOVE_USER_FROM_GROUPS)]: (draft, { payload }) => {
    draft.flags.user.updating_groups = false;
    draft.users.selected = mapLoadedUser(payload);
  },
  [types.fail(REMOVE_USER_FROM_GROUPS)]: (draft, { payload }) => {
    draft.flags.user.updating_groups = false;
    draft.debug.groups_update.errors = payload;
  },
  [types.loading(CREATE_USER)]: draft => {
    draft.flags.user.creating = true;
  },
  [types.success(CREATE_USER)]: draft => {
    draft.flags.user.creating = false;
  },
  [types.fail(CREATE_USER)]: (draft, { payload }) => {
    draft.flags.user.creating = false;
    draft.debug.user.errors = payload;
  },
  [types.loading(UPDATE_USER_EXPIRATION)]: draft => {
    draft.flags.user.updateExpiration = true;
  },
  [types.success(UPDATE_USER_EXPIRATION)]: draft => {
    draft.flags.user.updateExpiration = false;
  },
  [types.fail(UPDATE_USER_EXPIRATION)]: (draft) => {
    draft.flags.user.updateExpiration = false;
  },
  [types.loading(UPDATE_GROUP_CONNECTIONS_LIMIT)]: draft => {
    draft.flags.group.updateConnectionsLimit = true;
  },
  [types.success(UPDATE_GROUP_CONNECTIONS_LIMIT)]: draft => {
    draft.flags.group.updateConnectionsLimit = false;
  },
  [types.fail(UPDATE_GROUP_CONNECTIONS_LIMIT)]: (draft) => {
    draft.flags.group.updateConnectionsLimit = false;
  }
};

export default createReducer(producers, initialState);
