import { mdiServer } from '@mdi/js';
import SvgIcon from '@material-ui/core/SvgIcon';
import React from 'react';
import Autocomplete from '@material-ui/lab/Autocomplete';
import { Input } from '../../../../shared/components/controls';
import { FullScreenIcon } from '../../FullScreenIcon/FullScreenIcon';

import './NetworkToolbar.local.scss';

export const NetworkToolbar = ({ servers, serversUpdate, fullScreen, fullScreenToggle }) => {
  const [selectedServers, setSelectedServers] = React.useState([]);
  const selectedServerIds = selectedServers.map(server => server.value);
  const serverOptions = (servers || [])
    .filter(server => !selectedServerIds.includes(server.id))
    .map(server => ({ value: server.id, label: server.label }));

  const onServersChange = (event, newValue) => {
    setSelectedServers(newValue);
    serversUpdate(newValue);
  };

  return (
    <div className="network-toolbar">
      <div className="network-toolbar-filter-container">
        <Autocomplete
          style={{ width: 400 }}
          size="small"
          limitTags={2}
          disableClearable
          multiple
          id="server-filter"
          options={serverOptions}
          value={selectedServers}
          getOptionLabel={opt => opt.label || ''}
          onChange={onServersChange}
          renderInput={params => (
            <Input
              {...params}
              size="small"
              label={
                <span className="servers-filter-wrap">
                  <SvgIcon>
                    <path d={mdiServer} />
                  </SvgIcon>
                  <span className="servers-filter-label">Server</span>
                </span>
              }
              variant="outlined"
            />
          )}
        />
      </div>
      <FullScreenIcon fullScreen={fullScreen} fullScreenToggle={fullScreenToggle} />
    </div>
  );
};
