import React from 'react';
import { Header } from 'core/components/Header';
import { Main, Paper } from 'shared/components/layout';
import { connect } from 'react-redux';
import { loadProcessDetailsThunk } from 'explorer/store/thunks';
import { Center, LightTooltip } from '../../shared/components';
import { Loader } from '../../shared/components/loaders';
import { formatParameters, getInitialValues } from '../../license-collection/utils/formikFormatter';
import { Form, Formik } from 'formik';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import InputAdornment from '@material-ui/core/InputAdornment';
import SvgIcon from '@material-ui/core/SvgIcon';
import Icon from '@mdi/react';
import { mdiHelpCircleOutline, mdiMemory, mdiPlaySpeed } from '@mdi/js';
import Button from '@material-ui/core/Button';
import normalizeParameter from '../../license-collection/utils/normalizeParameter';
import _ from 'lodash';
import { TextField as Input } from '../../license-collection/controls/TextField/TextField';
import { DropdownField } from '../../license-collection/controls/DropdownField/DropdownField';
import { CONSTANTS } from 'config';
import { RestClient } from '../../shared/lib/rest';
import { executeCollectionProcessThunk } from '../../explorer/store/thunks';
import { OutputModal } from '../../license-collection/components/OutputModal/OutputModal';
import { FormControlLabel, Radio, RadioGroup } from '@material-ui/core';
import { fetchEntity } from '../../entities/actions';

import './MssqlUsageCollection.local.scss';

const SINGLE_SERVER_PROCESS = 'vendita.microsoft.sqlserver.util.CoreVersionRpt';
const MULTI_SERVER_PROCESS = 'vendita.microsoft.sqlserver.util.multiDBLicenseScan';

export class MssqlUsageCollection extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      invocation: undefined,
      singleServer: true,
      outputOpen: false,
      output: undefined
    };
  }

  async componentDidMount() {
    const { requestProcessData, singleServerProcess, entitiesStatus, request } = this.props;
    if (entitiesStatus.prototype === 'not-fetched' || entitiesStatus.prototype === 'failed') {
      request('prototype');
    }

    if (entitiesStatus.type === 'not-fetched' || entitiesStatus.type === 'failed') {
      request('type');
    }

    if (entitiesStatus.account === 'not-fetched' || entitiesStatus.account === 'failed') {
      request('account');
    }

    if (entitiesStatus.form === 'not-fetched' || entitiesStatus.form === 'failed') {
      request('form');
    }

    if (!singleServerProcess) {
      requestProcessData({ name: 'mssqlSingleServerUsage', process: SINGLE_SERVER_PROCESS });
    }
  }

  async componentDidUpdate() {
    const {
      requestProcessData,
      singleServerProcess,
      multiServerProcess,
      isSingleProcessLoading,
      isMultiProcessLoading
    } = this.props;
    const { singleServer } = this.state;

    if (singleServer && !singleServerProcess && !isSingleProcessLoading) {
      requestProcessData({ name: 'mssqlSingleServerUsage', process: SINGLE_SERVER_PROCESS });
    } else if (!singleServer && !multiServerProcess && !isMultiProcessLoading) {
      requestProcessData({ name: 'mssqlMultiServerUsage', process: MULTI_SERVER_PROCESS });
    }
  }

  componentWillUnmount() {
    // to conclude the polling,
    clearTimeout(this.poller);
    this.poller = undefined;
  }

  render() {
    const {
      isSingleProcessLoading,
      isMultiProcessLoading,
      singleServerProcess,
      multiServerProcess,
      datatypes,
      prototypes,
      executeCollectionProcess
    } = this.props;
    const { singleServer } = this.state;
    const selectedProcessName = singleServer ? SINGLE_SERVER_PROCESS : MULTI_SERVER_PROCESS;

    if (singleServer && !singleServerProcess && !isSingleProcessLoading) {
      return <Center>{`There is a problem with ${SINGLE_SERVER_PROCESS} process`}</Center>;
    }

    if (!singleServer && !multiServerProcess && !isMultiProcessLoading) {
      return <Center>{`There is a problem with ${MULTI_SERVER_PROCESS} process`}</Center>;
    }

    if (singleServer ? isSingleProcessLoading : isMultiProcessLoading) {
      return (
        <Center>
          <Loader>Loading {selectedProcessName} information</Loader>
        </Center>
      );
    }

    const currentProcess = singleServer ? singleServerProcess : multiServerProcess;
    let parameters = null;
    if (currentProcess) {
      parameters = currentProcess.parameters.map(p => normalizeParameter(p, datatypes, prototypes));
      parameters = _.sortBy(parameters, 'position');
    }

    return (
      <>
        <Header title="Compliance / Microsoft / Usage Collection" />
        <Main>
          <Formik
            initialValues={getInitialValues(parameters)}
            onSubmit={async values => {
              try {
                const payload = {
                  process: selectedProcessName,
                  parameters: formatParameters(values),
                  timestamp: null
                };
                const response = await RestClient.post(CONSTANTS.BASE.ENDPOINTS.JOBS, payload);
                executeCollectionProcess({ name: 'hardware' });

                if (
                  response.data.data &&
                  response.data.data.invocations &&
                  response.data.data.invocations[0]
                ) {
                  this.setState(
                    {
                      invocation: response.data.data.invocations[0],
                      outputOpen: true,
                      output: undefined
                    },
                    () => {
                      this.runPoller();
                    }
                  );
                }
              } catch (error) {
                console.log('[MSSQL Usage Collection] submit form error: ', error); // eslint-disable-line
              }
            }}
          >
            {({ dirty, isSubmitting }) => {
              return (
                <Form className="mssql-usage-collection-form">
                  <Paper>
                    <Paper.Content>
                      <RadioGroup
                        row
                        className="process-type"
                        value={singleServer ? 'single' : 'multi'}
                        onChange={(event) => {
                          this.setState({ singleServer: event.target.value === 'single' });
                        }}
                      >
                        <FormControlLabel value="single" control={<Radio color="primary" size="small" />} label="Single server" />
                        <FormControlLabel value="multi" control={<Radio color="primary" size="small" />} label="Multiple servers" />
                      </RadioGroup>

                      <div className="info">
                        <h1>Job</h1>
                        <p>{singleServer ? 'Single' : 'Multi'} server usage collection</p>
                      </div>
                      <FormControl className="field full-width" variant="outlined">
                        <InputLabel htmlFor="outlined-adornment-process">Process</InputLabel>
                        <OutlinedInput
                          id="outlined-adornment-process"
                          type="text"
                          readOnly
                          disabled
                          value={selectedProcessName}
                          labelWidth={70}
                          startAdornment={
                            <InputAdornment position="start">
                              <SvgIcon>
                                <Icon path={mdiPlaySpeed} />
                              </SvgIcon>
                            </InputAdornment>
                          }
                          endAdornment={
                            <LightTooltip placement="bottom-end" title={process.description}>
                              <InputAdornment position="end">
                                <SvgIcon>
                                  <Icon path={mdiHelpCircleOutline} />
                                </SvgIcon>
                              </InputAdornment>
                            </LightTooltip>
                          }
                        />
                      </FormControl>
                      <div className="info">
                        <h1>Job Parameters</h1>
                      </div>

                      <div className="form-fields-container">
                        {parameters.map(parameter => {
                          return (
                            <div key={parameter.name} className="form-field">
                              {this.chooseField(parameter, false)}
                            </div>
                          );
                        })}
                      </div>
                    </Paper.Content>

                    <Paper.Footer>
                      <Button
                        className="btn output"
                        disabled={!this.state.invocation}
                        data-qa="view-output"
                        onClick={() => this.setState({ outputOpen: true })}
                      >
                        View Output
                      </Button>
                      <Button
                        className="btn submit"
                        type="submit"
                        disabled={!dirty || isSubmitting}
                        variant="contained"
                        data-qa="submit"
                      >
                        Submit
                      </Button>
                    </Paper.Footer>
                  </Paper>
                </Form>
              );
            }}
          </Formik>
          {this.renderModal({
            title: `Microsoft Usage Collection ${singleServer ? '(single server)' : '(multiple servers)'}`,
            icon: (
              <SvgIcon>
                <Icon path={mdiMemory} />
              </SvgIcon>
            )
          }, selectedProcessName)}
        </Main>
      </>
    );
  }

  runPoller = () => {
    const { uuid, status, date_invoke } = this.state.invocation;

    if (uuid === null) return;
    if (typeof status !== 'string') return;
    if (typeof date_invoke !== 'string') return;

    this.poller = setTimeout(this.pollInvocation(uuid), 3000);
  };

  pollInvocation = uuid => async () => {
    let { invocation } = this.state;

    if (!invocation || invocation.uuid !== uuid) return;

    try {
      const response = await RestClient.get(`${CONSTANTS.BASE.ENDPOINTS.JOBS}/${uuid}`);

      if (
        response.data.data &&
        response.data.data.invocations &&
        response.data.data.invocations[0]
      ) {
        invocation = response.data.data.invocations[0]; // eslint-disable-line

        if (invocation.status === 'SUCCEEDED' || invocation.status === 'FAILED') {
          const outputReponse = await RestClient.get(
            `${CONSTANTS.BASE.ENDPOINTS.JOBS}/${uuid}/display`,
            { page_size: 2147483645 }
          );
          if (
            outputReponse.data.data &&
            outputReponse.data.data.outputs &&
            outputReponse.data.data.outputs.length > 0
          ) {
            this.setState({ output: outputReponse.data.data.outputs, invocation });
            // to conclude the polling,
            clearTimeout(this.poller);
            this.poller = undefined;
          } else {
            this.setState({ invocation });
            clearTimeout(this.poller);
            this.poller = undefined;
          }
        } else {
          this.setState({ invocation }, () => this.runPoller());
        }
      }
    } catch (error) {
      console.log('[pollInvocation] error: ', error); // eslint-disable-line
    }
  };

  renderModal = (modalDetails, processName) => {
    if (!this.state.invocation) return null;

    const actions = {
      open: () => {
        this.setState({ outputOpen: true });
      },
      close: () => {
        this.setState({ outputOpen: false });
      }
    };

    return (
      <OutputModal
        open={this.state.outputOpen}
        actions={actions}
        output={this.state.output}
        invocation={this.state.invocation}
        modalDetails={modalDetails}
        processName={processName}
        messages={['Preparing job...', 'Executing job...', 'Loading job output...']}
      />
    );
  };

  chooseField(parameter, disabled) {
    if (parameter.data_type === 'string') {
      return <Input parameter={parameter} disabled={disabled} />;
    }

    if (parameter.data_type === 'mas0300.fmt.format' || parameter.data_type === 'boolean') {
      return <Input parameter={parameter} disabled />;
    }

    return (
      <DropdownField
        parameter={parameter}
        outputOpen
        datatypes={this.props.datatypes}
        rawAccounts={this.props.rawAccounts}
        prototypes={this.props.prototypes}
        forms={this.props.forms}
        disabled={disabled}
      />
    );
  }
}

export const ConnectedMssqlUsageCollection = connect(
  state => ({
    prototypes: state.entities.data?.prototype?.data?.data?.prototypes || [],
    datatypes: state.entities.data?.type?.data?.data?.data_types || [],
    rawAccounts: state.entities.data?.account?.data?.data?.accounts || [],
    forms: state.entities.data?.form?.data?.data?.forms || [],
    entitiesStatus: state.entities.fetch_status,
    singleServerProcess: state.explorer.collectionProcesses.mssqlSingleServerUsage,
    multiServerProcess: state.explorer.collectionProcesses.mssqlMultiServerUsage,
    isSingleProcessLoading: state.explorer.flags.collectionProcesses.mssqlSingleServerUsage.loading,
    isMultiProcessLoading: state.explorer.flags.collectionProcesses.mssqlMultiServerUsage.loading
  }),
  dispatch => ({
    requestProcessData: data => dispatch(loadProcessDetailsThunk(data)),
    executeCollectionProcess: data => dispatch(executeCollectionProcessThunk(data)),
    request: data => dispatch(fetchEntity(data)),
  })
)(MssqlUsageCollection);
