import React, { PureComponent } from 'react';
import { Cell, Pie, PieChart, ResponsiveContainer, Tooltip } from 'recharts';
import './DbVersionAndOsTile.local.scss';
import {
  DB_VERSION_COLOR_PALETTE,
  LINUX_OS_PRIMARY_COLOR,
  LINUX_OS_SECONDARY_COLOR,
  WINDOWS_OS_PRIMARY_COLOR,
  WINDOWS_OS_SECONDARY_COLOR
} from '../../OperatingSystemsTile/OperatingSystemsConstants';
import { Overflow } from 'shared/components/layout';
import { OptionsList } from '../../OptionsTile/components/OptionsList/OptionsList';
import { ChartTooltip } from '../../ChartTooltip/ChartTooltip';
import { connect } from 'react-redux';
import { loadMssqlDbVersionsThunk, loadMssqlOsTypesThunk } from '../../../store/thunks';
import { Center } from 'shared/components';
import { Loader } from 'shared/components/loaders';

class MssqlDbVersionAndOsTile extends PureComponent {
  componentDidMount() {
    const { getOSType, getDBVersion } = this.props;
    getOSType();
    getDBVersion();
  }

  UNSAFE_componentWillReceiveProps(newProps) {
    if (newProps.selectedServer !== this.props.selectedServer) {
      const { getOSType, getDBVersion } = this.props;
      setTimeout(() => {
        getOSType(newProps.selectedServer);
        getDBVersion(newProps.selectedServer);
      });
    }
  }

  render() {
    const { isLoading, dbVersions, osTypes } = this.props;

    if (isLoading) {
      return (
        <>
          <div className="exec-dashboard-graph-container mssql-db-version-and-os-graph-container">
            <Center>
              <Loader>Loading Data...</Loader>
            </Center>
          </div>
        </>
      );
    }

    const preparedOSData = this.mapOS(osTypes);
    const preparedDBVersionsData = this.mapDBVersion(dbVersions);
    const hasData = preparedDBVersionsData.length;

    return (
      <div className="exec-dashboard-graph-container mssql-db-version-and-os-graph-container">
        {hasData ? (
          <div className="charts-container">
            <div className="chart">
              <div className="chart-title">Operating System</div>
              <div className="graph-legend-container">
                <Overflow>
                  <OptionsList options={preparedOSData} />
                </Overflow>
              </div>
              <ResponsiveContainer height={380}>
                <PieChart>
                  <Pie data={preparedOSData} dataKey="value" cx="50%" cy="60%" innerRadius={40} outerRadius={80} blendStroke>
                    {preparedOSData.map((entry, index) => (
                      <Cell key={`cell-${entry.name}-${index}`} fill={entry.color} />
                    ))}
                  </Pie>
                  <Tooltip content={ChartTooltip} />
                </PieChart>
              </ResponsiveContainer>
            </div>
            <div className="chart">
              <div className="chart-title">DB Version</div>
              <div className="graph-legend-container">
                <Overflow>
                  <OptionsList options={preparedDBVersionsData} />
                </Overflow>
              </div>
              <ResponsiveContainer height={380}>
                <PieChart>
                  <Pie data={preparedDBVersionsData} dataKey="value" cx="50%" cy="60%" innerRadius={40} outerRadius={80} blendStroke>
                    {preparedDBVersionsData.map((entry, index) => (
                      <Cell key={`cell-${index}`} fill={entry.color} />
                    ))}
                  </Pie>
                  <Tooltip content={ChartTooltip} />
                </PieChart>
              </ResponsiveContainer>
            </div>
          </div>
        ) : (
          <Center>
            No Data Available
          </Center>
        )}
      </div>
    );
  }

  mapOS = (osData) => {
    return (osData || []).map((os) => {
      const name = os.name || os.os || '';
      const isWindowsOS = name.indexOf('Windows') !== -1;
      return {
        ...os,
        isWindowsOS,
        value: os.value || os.count,
        label: name,
        color: isWindowsOS ? WINDOWS_OS_PRIMARY_COLOR : LINUX_OS_PRIMARY_COLOR,
        secondaryColor: isWindowsOS ? WINDOWS_OS_SECONDARY_COLOR : LINUX_OS_SECONDARY_COLOR,
      };
    });
  }

  mapDBVersion = (dbVersionData) => {
    return (dbVersionData || []).map((db, index) => {
      const isSupported = db.is_supported === 'yes';
      return {
        ...db,
        name: db.version_year,
        tooltip: `${db.version_year}${!isSupported ? ' (Not supported)' : ''}`,
        value: db.count,
        color: DB_VERSION_COLOR_PALETTE[index].color,
        secondaryColor: DB_VERSION_COLOR_PALETTE[index].secondaryColor,
        label: db.version_year,
        hasIssues: !isSupported
      };
    });
  }
}

export const MssqlConnectedDbVersionAndOsTile = connect(
  state => {
    return {
      isLoading:
        state.executiveDashboard.flags.mssqlDbVersions.loading ||
        state.executiveDashboard.flags.mssqlOsTypes.loading,
      dbVersions: state.executiveDashboard.mssqlDbVersions,
      osTypes: state.executiveDashboard.mssqlOsTypes,
    };
  },
  dispatch => ({
    getOSType: (serverId) => dispatch(loadMssqlOsTypesThunk(serverId)),
    getDBVersion: (serverId) => dispatch(loadMssqlDbVersionsThunk(serverId)),
  })
)(MssqlDbVersionAndOsTile);
