import React from 'react';
import PropTypes from 'prop-types';
import { useLocation } from 'react-router-dom';
import moment from 'moment';
import { useDispatch } from 'react-redux';
import { actions } from 'shared/store/actions';

export const JobsInvocations = ({ children }) => {
  const location = useLocation();
  const query = new URLSearchParams(location.search);

  const dispatch = useDispatch();
  const getJobs = data => {
    dispatch(actions.getJobs.dispatchLoading(data));
    dispatch(actions.getConnections.dispatchLoading(data));
  };

  React.useEffect(() => {
    const startDate = moment.unix(moment().subtract(7, 'days').startOf('day').unix()).format('YYYY-MM-DD');
    const endDate = moment.unix(moment().endOf('day').unix()).format('YYYY-MM-DD');
    const days = moment(endDate).diff(moment(startDate), 'days');

    getJobs({
      date_invoke: startDate,
      period: days + 1,
      page_size: 2147483645,
      shell_host_address_filter: query.get('shell_host_address_filter') || '',
      platform_name_filter: query.get('platform_filter_name') || '',
      platform_dbms_filter: query.get('platform_filter_dbms') || '',
      status: query.get('status') || '',
      database_name_filter: query.get('database_name_filter') || '',
      owner: query.get('user_filter') || '',
      name_filter: query.get('name_filter') || ''
    });
  }, []);

  return children;
};
JobsInvocations.propTypes = {
  children: PropTypes.any.isRequired
};
