import React, { PureComponent } from 'react';
import Graph from 'react-graph-vis';
import {
  BASE_NETWORK_GRAPH_OPTIONS,
  DB_NODE_COLOR,
  DB_NODE_SHAPE,
  NODE_SIZE_MULTIPLIER,
  ROOT_NODE,
  ROOT_NODE_ID,
  SERVER_NODE_COLOR,
  SERVER_NODE_SHAPE
} from '../../ServersNetworkTile/ServersNetworkConstants';
import { connect } from 'react-redux';
import { Center } from 'shared/components';
import { Loader } from 'shared/components/loaders';
import {
  loadMssqlNetworkEdgesThunk,
  loadMssqlNetworkNodesThunk,
  loadMssqlServerDetailsThunk
} from '../../../store/thunks';

import 'vis-network/styles/vis-network.css';
import './ServersNetworkTile.local.scss';
import classnames from 'classnames';
import { NetworkToolbar } from '../../ServersNetworkTile/Toolbar/NetworkToolbar';

class MssqlServersNetworkTile extends PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      fullScreen: false,
      filteredServerIds: []
    };
  }

  events = {
    select: (event) => {
      const { nodes } = event;
      if (nodes.length) {
        const nodeId = nodes[0];
        const selectedNode = this.networkData.graph.nodes.find((node) => node.id === nodeId);
        if (selectedNode && selectedNode.isServerNode) {
          const { onServerChanged, getServerDetails } = this.props;
          onServerChanged(selectedNode.id);
          getServerDetails(selectedNode.id);
        }
      }
    }
  };

  componentDidMount() {
    const { getNetworkNodes, getNetworkEdges } = this.props;
    getNetworkNodes();
    getNetworkEdges();
  }

  UNSAFE_componentWillReceiveProps(newProps) {
    if (newProps.selectedServer !== this.props.selectedServer) {
      const { getNetworkNodes, getNetworkEdges } = this.props;
      setTimeout(() => {
        getNetworkNodes(newProps.selectedServer);
        getNetworkEdges(newProps.selectedServer);
      });
    }
  }

  render() {
    const { nodes, edges, selectedServer, serverDetails, isLoading } = this.props;

    if (isLoading) {
      return (
        <>
          <div className="exec-dashboard-graph-container mssql-servers-network-graph-container">
            <Center>
              <Loader>Loading Network Data...</Loader>
            </Center>
          </div>
        </>
      );
    }

    const { fullScreen, filteredServerIds } = this.state;
    let preparedNodes = this.mapNodes(nodes);
    const serverNodes = preparedNodes.filter((node) => node.isServerNode);
    const hasData = preparedNodes.length && edges.length;

    if (preparedNodes.length && !selectedServer) {
      if (filteredServerIds.length) {
        const relatedDbIds = edges.filter((edge) => filteredServerIds.includes(edge.from)).map((edge) => edge.to);
        preparedNodes = preparedNodes.filter((node) => filteredServerIds.includes(node.id) || relatedDbIds.includes(node.id));
      }
      preparedNodes.unshift(ROOT_NODE);
    }

    this.networkData = {
      graph: {
        nodes: preparedNodes,
        edges: this.mapEdges(edges, serverNodes),
      }
    };

    return (
      <div className={classnames('exec-dashboard-graph-container mssql-servers-network-graph-container', { 'show-full-screen': fullScreen })}>
        {hasData ? (
          <>
            <NetworkToolbar
              servers={serverNodes}
              serversUpdate={(servers) => {
                const serverIds = servers.map((s) => s.value);
                this.setState({ filteredServerIds: serverIds });
              }}
              fullScreen={fullScreen}
              fullScreenToggle={() => this.setState({ fullScreen: !fullScreen })}
            />
            <div className="graph-wrapper">
              <Graph
                graph={this.networkData.graph}
                options={BASE_NETWORK_GRAPH_OPTIONS}
                events={this.events}
              />
            </div>
            {selectedServer && serverDetails && Object.keys(serverDetails).length ? (
              <div className="server-details-container">
                  <div className="server-details-row">Chipset: {serverDetails.chipset}</div>
                  <div className="server-details-row">CPU Speed: {serverDetails.cpu_speed}GHz</div>
                  <div className="server-details-row">Physical Cores: {serverDetails.physical_cores}</div>
                  <div className="server-details-row">Logical Cores: {serverDetails.logical_cores}</div>
                  <div className="server-details-row">Server Name: {serverDetails.servername}</div>
                  <div className="server-details-row">Server Entity Name: {serverDetails.server_entity_name}</div>
              </div>
            ) : ''}
          </>
        ) : (
          <Center>
            No Network Data Available
          </Center>
        )}
      </div>
    );
  }

  mapNodes = (nodes) => {
    return (nodes || [])
      .filter((node, index, arr) => {
        return arr.findIndex(t => (t.id === node.id)) === index;
      })
      .map((node) => {
        const isServerNode = node.title === 'server';
        const hasWarnings = node.warning === 'yes';
        const nodeConfig = {
          ...node,
          isServerNode,
          title: node.label,
          widthConstraint: node.size * NODE_SIZE_MULTIPLIER,
          color: {
            background: isServerNode ? SERVER_NODE_COLOR : DB_NODE_COLOR,
            border: isServerNode ? SERVER_NODE_COLOR : DB_NODE_COLOR
          },
          shape: isServerNode ? SERVER_NODE_SHAPE : DB_NODE_SHAPE,
        };

        if (hasWarnings) {
          nodeConfig.borderWidth = 3;
          nodeConfig.color.border = 'red';
          nodeConfig.title += ' (There is more usage than licenses allocated)';
        }

        return nodeConfig;
      });
  }

  mapEdges = (edges, serverNodes) => {
    const serverEdges = serverNodes.map((node) => {
      return { from: ROOT_NODE_ID, to: node.id, width: 5, arrows: { to: { enabled: false } } };
    });
    return edges.map((edge) => {
      return { from: edge.from, to: edge.to, width: 5, arrows: { to: { enabled: false } } };
    }).concat(serverEdges);
  }
}

export const MssqlConnectedServersNetworkTile = connect(
  state => {
    return {
      isLoading:
        state.executiveDashboard.flags.mssqlNetwork.nodesLoading ||
        state.executiveDashboard.flags.mssqlNetwork.edgesLoading,
      nodes: state.executiveDashboard.mssqlNetworkNodes,
      edges: state.executiveDashboard.mssqlNetworkEdges,
      serverDetails: state.executiveDashboard.mssqlNetworkServerDetails,
    };
  },
  dispatch => ({
    getNetworkNodes: (serverId) => dispatch(loadMssqlNetworkNodesThunk(serverId)),
    getNetworkEdges: (serverId) => dispatch(loadMssqlNetworkEdgesThunk(serverId)),
    getServerDetails: (serverId) => dispatch(loadMssqlServerDetailsThunk(serverId)),
  })
)(MssqlServersNetworkTile);
