import React from 'react';
import { connect } from 'react-redux';
import { Form, Formik } from 'formik';
import { Column, Row, Section } from 'shared/components/layout';
import { Text } from 'shared/components/form/editors/Generic';
import { Button } from 'shared/components/controls';

import './ConnectionsNumber.local.scss';
import { updateGroupConnectionsLimitThunk } from '../../../../store/thunks';

const ConnectionsNumber = ({ groupName, connectionsLimit, updateConnectionsLimit, updateInProgress }) => {
  const initialValues = {
    maxAmount: connectionsLimit || 0,
  };

  return (
    <Section>
      <Section.Title>Number of Connections</Section.Title>
        <div className="connections-number">
          <Formik
            initialValues={initialValues}
            onSubmit={(values) => {
              updateConnectionsLimit(groupName, values.maxAmount);
            }}
          >
            {() => {
              return (
                <>
                  <Form>
                    <Row compact>
                      <Column xs={10}>
                        <Text name="maxAmount" label="Enter max number of connections" required value={initialValues.maxAmount} />
                      </Column>
                      <Column xs={2}>
                        <Button type="submit" variant="contained" className="update-connections-btn" disabled={updateInProgress}>Update</Button>
                      </Column>
                    </Row>
                  </Form>
                </>
              );
            }}
          </Formik>
        </div>
    </Section>
  );
};

export const ConnectionsNumberContainer = connect(
  state => ({
    groupName: state.groups.selected.name,
    connectionsLimit: state.groups.selected.connection_limit,
    updateInProgress: state.usersManager.flags.group.updateConnectionsLimit
  }),
  dispatch => ({
    updateConnectionsLimit: (group, limit) => dispatch(updateGroupConnectionsLimitThunk(group, limit))
  })
)(ConnectionsNumber);
