import React, { PureComponent } from 'react';
import { Bar, BarChart, CartesianGrid, Cell, Legend, ResponsiveContainer, Tooltip, XAxis, YAxis } from 'recharts';
import { connect } from 'react-redux';
import { loadLicenseDeltaThunk } from '../../store/thunks';
import {
  LICENSABLE_FEATURE_DEFAULT_COLOR,
  LICENSABLE_FEATURE_DEFAULT_SECONDARY_COLOR,
  LICENSABLE_FEATURES_COLORS
} from '../LicensableFeaturesGraph/LicensableFeaturesConstants';
import { Center, LightTooltip } from '../../../shared/components';
import { Loader } from '../../../shared/components/loaders';
import { FormControlLabel, Radio, RadioGroup } from '@material-ui/core';
import { FullScreenIcon } from '../FullScreenIcon/FullScreenIcon';
import classnames from 'classnames';

import './LicenseDeltaTile.local.scss';

const LicenseType = {
  NUP: 'nup',
  CPU: 'cpu',
};

export const LicenseDeltaChartTooltip = (data) => {
  if (!data || !data.payload || data.payload.length < 3) {
    return '';
  }

  const assignedValue = data.payload[0].value;
  const usedValue = data.payload[1].value;
  const ownedValue = data.payload[2].value;
  return (
    <div className="exec-dashboard-chart-tooltip">
      <LightTooltip placement="bottom-end">
        <div className="exec-dashboard-chart-tooltip-content">
          <div className="exec-dashboard-chart-tooltip-row">
            <div className="exec-dashboard-chart-tooltip-name">Assigned</div>
            <div className="exec-dashboard-chart-tooltip-value">{assignedValue}</div>
          </div>
          <div className="exec-dashboard-chart-tooltip-row">
            <div className="exec-dashboard-chart-tooltip-name">Used</div>
            <div className="exec-dashboard-chart-tooltip-value">{usedValue}</div>
          </div>
          <div className="exec-dashboard-chart-tooltip-row">
            <div className="exec-dashboard-chart-tooltip-name">Owned</div>
            <div className="exec-dashboard-chart-tooltip-value">{ownedValue}</div>
          </div>
        </div>
      </LightTooltip>
    </div>
  );
};

class LicenseDeltaTile extends PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      selectedLicenseType: LicenseType.NUP,
      fullScreen: false,
    };
  }

  componentDidMount() {
    const { getLicenseDelta } = this.props;
    getLicenseDelta();
  }

  UNSAFE_componentWillReceiveProps(newProps) {
    if (newProps.selectedServer !== this.props.selectedServer) {
      const { getLicenseDelta } = this.props;
      setTimeout(() => getLicenseDelta(newProps.selectedServer));
    }
  }

  render() {
    const { licenseDelta, selectedServer, isLoading } = this.props;
    const { selectedLicenseType, fullScreen } = this.state;
    if (isLoading) {
      return (
        <>
          <div className="exec-dashboard-graph-container license-delta-graph-container">
            <Center>
              <Loader>Loading License Data...</Loader>
            </Center>
          </div>
        </>
      );
    }

    const chartData = this.mapData(licenseDelta, selectedLicenseType);
    return (
      <div className={classnames('exec-dashboard-graph-container license-delta-graph-container', { 'show-full-screen': fullScreen })}>
        <RadioGroup
          row
          className="license-type"
          value={selectedLicenseType}
          onChange={(event) => this.setState({ selectedLicenseType: event.target.value })}
        >
          <FormControlLabel value="nup" control={<Radio color="primary" size="small" />} label="NUPs" />
          <FormControlLabel value="cpu" control={<Radio color="primary" size="small" />} label="CPUs" />
        </RadioGroup>

        <FullScreenIcon fullScreen={fullScreen} fullScreenToggle={() => this.setState({ fullScreen: !fullScreen })} />

        {chartData.length ? (
          <>
            <div className="chart-title">License Delta</div>
            <ResponsiveContainer height={fullScreen ? 700 : 380}>
              <BarChart data={chartData} barGap={0} margin={{ left: 20 }}>
                <CartesianGrid strokeDasharray="3 3" vertical={false} />
                <XAxis dataKey="label" tickSize={false} axisLine={false} tick={this.renderCustomXAxisTick} />
                <YAxis
                  width={50}
                  tickSize={false}
                  allowDecimals={false}
                  type="number"
                  domain={[0, selectedServer ? 2 : 'dataMax']}
                  stroke="rgba(4, 17, 37, 0.87)"
                  label={{ value: 'Count of Feature in envt', angle: -90, position: 'left' }}
                />
                <Bar dataKey="assigned" fill="#13369f">
                  {chartData.map((entry, index) => {
                    return <Cell key={`${entry.name}-${index}`} fill="#13369f" />;
                  })}
                </Bar>
                <Bar dataKey="used" fill="#f2c94c">
                  {chartData.map((entry, index) => {
                    return <Cell key={`${entry.name}-${index}`} fill="#f2c94c" />;
                  })}
                </Bar>
                <Bar dataKey="owned" fill="#55c667">
                  {chartData.map((entry, index) => {
                    return <Cell key={`${entry.name}-${index}`} fill="#55c667" />;
                  })}
                </Bar>
                <Tooltip content={LicenseDeltaChartTooltip} cursor={false} />
                <Legend />
              </BarChart>
            </ResponsiveContainer>
          </>
        ) : (
          <Center>
            No Data Available
          </Center>
        )}
      </div>
    );
  }

  renderCustomXAxisTick = ({ x, y, payload }) => {
    const licenseConfig = LICENSABLE_FEATURES_COLORS.find((config) => config.type === payload.value);
    return (
      <g transform={`translate(${x},${y})`}>
        <text className="x-axis-label" x={0} y={0} dy={20} textAnchor="middle" fill={licenseConfig?.color || '#aaa'}>
          {payload.value}
        </text>
      </g>
    );
  };

  mapData = (licenseData, selectedType) => {
    const groupedData = (licenseData || [])
      .reduce((acc, license) => {
        const key = `${license.product_id}_${license.metric}`;
        if (acc[key]) {
          const updated = { ...acc[key] };
          updated.lic_owned += license.lic_owned;
          updated.lic_used += license.lic_used;
          updated.lic_assigned += license.lic_assigned;
          acc[key] = updated;
        } else {
          acc[key] = { ...license, type: license.metric === 'Named User Plus' ? LicenseType.NUP : LicenseType.CPU };
        }
        return acc;
      }, {});

    return Object.values(groupedData)
      .filter((license) => license.type === selectedType)
      .map((license) => {
        const licenseConfig = LICENSABLE_FEATURES_COLORS.find((config) => license.abbreviation === config.type);
        return {
          name: license.product_name,
          label: license.abbreviation,
          assigned: license.lic_assigned,
          used: license.lic_used,
          owned: license.lic_owned,
          color: licenseConfig ? licenseConfig.color : LICENSABLE_FEATURE_DEFAULT_COLOR,
          secondaryColor: licenseConfig ? licenseConfig.secondaryColor : LICENSABLE_FEATURE_DEFAULT_SECONDARY_COLOR,
        };
      });
  };
}

export const ConnectedLicenseDeltaTile = connect(
  state => {
    return {
      isLoading: state.executiveDashboard.flags.licenseDelta.loading,
      licenseDelta: state.executiveDashboard.licenseDelta,
    };
  },
  dispatch => ({
    getLicenseDelta: (serverId) => dispatch(loadLicenseDeltaThunk(serverId)),
  })
)(LicenseDeltaTile);
