import React from 'react';
import { connect } from 'react-redux';
import { updateUserExpirationThunk } from '../../../../store/thunks';
import { Form, Formik } from 'formik';
import { Column, Row } from 'shared/components/layout';
import { Date, Time } from 'shared/components/form/editors/Generic';
import { Button } from 'shared/components/controls';
import moment from 'moment';

import './AccountExpiration.local.scss';

const AccountExpiration = ({ userName, expirationDate, updateExpirationDate, updateInProgress }) => {
  let dateValue = null;
  let timeValue = null;

  if (expirationDate) {
    const expirationMoment = moment.utc(expirationDate, 'YYYY-MM-DDThh:mm:ss');
    dateValue = expirationMoment.format('MM/DD/YYYY');
    timeValue = expirationMoment.format('hh:mm');
  }

  const initialValues = {
    expirationDate: dateValue,
    expirationTime: timeValue,
  };

  const dateTimeValidations = (values) => {
    const errors = {};
    const date = values.expirationDate;
    const time = values.expirationTime;

    if (!moment(date).isValid()) {
      errors.expirationDate = 'Invalid date format';
    } else if (moment(date).isSameOrBefore(moment.utc())) {
      errors.expirationDate = 'Date should be in future';
    }

    if (!/^(2[0-3]|[01]?[0-9]):([0-5]?[0-9])$/.test(time)) {
      errors.expirationTime = 'Invalid time format';
    }

    return errors;
  };

  return (
    <div className="account-expiration">
      <Formik
        initialValues={initialValues}
        validate={dateTimeValidations}
        onSubmit={(values) => {
          const expirationMoment = moment.utc(`${values.expirationDate} ${values.expirationTime}`, 'MM/DD/YYYY hh:mm');
          updateExpirationDate(userName, expirationMoment.format('YYYY-MM-DD hh:mm:ss'));
        }}
      >
        {() => {
          return (
            <>
              <Form>
                <Row compact>
                  <Column xs={5}>
                    <Date
                      name="expirationDate"
                      label="Date"
                      value={initialValues.expirationDate}
                      minDate={moment()}
                      minDateMsg="Date should be in future"
                      allowBlankValue
                    />
                  </Column>
                  <Column xs={5}>
                    <Time
                      name="expirationTime"
                      label="Time (UTC)"
                      value={initialValues.expirationTime}
                      allowBlankValue
                    />
                  </Column>
                  <Column xs={2}>
                    <Button type="submit" variant="contained" className="update-expiration-btn" disabled={updateInProgress}>Update</Button>
                  </Column>
                </Row>
              </Form>
            </>
          );
        }}
      </Formik>
    </div>
  );
};

export const AccountExpirationContainer = connect(
  state => ({
    userName: state.usersManager.users.selected.name,
    expirationDate: state.usersManager.users.selected.expiration_datetime,
    updateInProgress: state.usersManager.flags.user.updateExpiration
  }),
  dispatch => ({
    updateExpirationDate: (user, newDate) => dispatch(updateUserExpirationThunk(user, newDate))
  })
)(AccountExpiration);
