import React from 'react';
import SvgIcon from '@material-ui/core/SvgIcon';
import { mdiFullscreen, mdiFullscreenExit } from '@mdi/js';
import Icon from '@mdi/react';
import { LightTooltip } from 'shared/components';

import './FullScreenIcon.local.scss';

export const FullScreenIcon = ({ fullScreen, fullScreenToggle }) => {
  return (
    <>
      <LightTooltip placement="top" title={fullScreen ? 'Collapse' : 'Expand'}>
        <SvgIcon className="full-screen-icon" onClick={fullScreenToggle}>
          {fullScreen ? (
            <Icon path={mdiFullscreenExit} />
          ) : (
            <Icon path={mdiFullscreen} />
          )}
        </SvgIcon>
      </LightTooltip>
    </>
  );
};
