import React, { useState } from 'react';
import { connect } from 'react-redux';
import { Center, Switch } from 'shared/components';
import { Loader } from 'shared/components/loaders';
import { Main } from 'shared/components/layout';
import { Result } from 'shared/components/overlays/Result/Result';
import { Header } from 'core/components/Header/Header';
import { ToggleSelection } from './containers/ToggleSelection/ToggleSelection';
import { ConnectedServersNetworkTile } from './components/ServersNetworkTile/ServersNetworkTile';
import { ConnectedLicensableFeaturesTile } from './components/LicensableFeaturesGraph/LicensableFeaturesTile';
import { ConnectedOperatingSystemsTile } from './components/OperatingSystemsTile/OperatingSystemsTile';
import { ConnectedLicenseDeltaTile } from './components/LicenseDeltaTile/LicenseDeltaTile';
import { DbmsType } from '../explorer/SystemExplorerFeatureUtil';
import { MssqlConnectedLicenseUsageTile } from './components/MSSQL/LicenseUsageTile/LicenseUsageTile';
import { MssqlConnectedServersNetworkTile } from './components/MSSQL/ServersNetworkTile/ServersNetworkTile';
import { MssqlConnectedEnvironmentUsageTile } from './components/MSSQL/EnvironmentUsageTile/EnvironmentUsageTile';
import { MssqlConnectedDbVersionAndOsTile } from './components/MSSQL/DbVersionAndOsTile/DbVersionAndOsTile';

import './ExecutiveDashboard.local.scss';

const prepareDashboardTitle = (dbType) => {
  switch (dbType) {
    case DbmsType.ORACLE: {
      return 'Compliance / Oracle / Executive Dashboard';
    }
    case DbmsType.MSSQL: {
      return 'Compliance / Microsoft / Executive Dashboard';
    }
    default: return '';
  }
};

function ExecutiveDashboard({
  isLoading,
  callFailed,
  dbType,
}) {
  dbType = dbType || DbmsType.ORACLE;
  const [selectedServer, setSelectedServer] = useState(null);
  const dashboardTitle = prepareDashboardTitle(dbType);

  return (
    <>
      <Header title={dashboardTitle}>
        {!isLoading && !callFailed && (
          <div className="header-actions-container">
            <ToggleSelection hasSelectedServer={!!selectedServer} deselectServer={() => setSelectedServer(null)} />
            {/*
              <RefreshDashboard onClickCallback={refreshDashboard} />
              <div className="separator" />
              <ToggleDashboard active={dashboardType} onClickCallback={changeDashboard} />
            */}
          </div>
        )}
      </Header>
      <Main mdPage>
        <Switch value={isLoading}>
          <Switch.Case value>
            <Center>
              <Loader>Loading Executive Dashboard...</Loader>
            </Center>
          </Switch.Case>
          <Switch.Case value={false}>
            <Switch value={callFailed}>
              <Switch.Case value>
                <Result type="fail">
                  <Result.Text>
                    Failed to load Executive Dashboard data. Please refresh the page and try again.
                  </Result.Text>
                </Result>
              </Switch.Case>
              <Switch.Case value={false}>
                <div className="widgets-container">
                  <div className="widget">
                    <Switch value={dbType}>
                      <Switch.Case value={DbmsType.ORACLE}>
                        <ConnectedLicensableFeaturesTile selectedServer={selectedServer} />
                      </Switch.Case>
                      <Switch.Case value={DbmsType.MSSQL}>
                        <MssqlConnectedLicenseUsageTile selectedServer={selectedServer} />
                      </Switch.Case>
                    </Switch>
                  </div>
                  <div className="widget">
                    <Switch value={dbType}>
                      <Switch.Case value={DbmsType.ORACLE}>
                        <ConnectedServersNetworkTile selectedServer={selectedServer} onServerChanged={(serverId) => setSelectedServer(serverId)} />
                      </Switch.Case>
                      <Switch.Case value={DbmsType.MSSQL}>
                        <MssqlConnectedServersNetworkTile selectedServer={selectedServer} onServerChanged={(serverId) => setSelectedServer(serverId)} />
                      </Switch.Case>
                    </Switch>
                  </div>
                  <div className="widget">
                    <Switch value={dbType}>
                      <Switch.Case value={DbmsType.ORACLE}>
                        <ConnectedLicenseDeltaTile selectedServer={selectedServer} />
                      </Switch.Case>
                      <Switch.Case value={DbmsType.MSSQL}>
                        <MssqlConnectedEnvironmentUsageTile selectedServer={selectedServer} />
                      </Switch.Case>
                    </Switch>
                  </div>
                  <div className="widget">
                    <Switch value={dbType}>
                      <Switch.Case value={DbmsType.ORACLE}>
                        <ConnectedOperatingSystemsTile selectedServer={selectedServer} />
                      </Switch.Case>
                      <Switch.Case value={DbmsType.MSSQL}>
                        <MssqlConnectedDbVersionAndOsTile selectedServer={selectedServer} />
                      </Switch.Case>
                    </Switch>
                  </div>
                </div>
              </Switch.Case>
            </Switch>
          </Switch.Case>
        </Switch>
      </Main>
    </>
  );
}

export const ConnectedExecutiveDashboard = connect(
  state => {
    return {
      isLoading:
        state.executiveDashboard.flags.dashboard.loading ||
        state.executiveDashboard.flags.virtual.loading,
      callFailed: state.executiveDashboard.flags.dashboard.callFailed,
      selected: state.executiveDashboard.selectedServers.length,
      rawEntries: state.executiveDashboard.rawEntries,
      rawVirtualServers: state.executiveDashboard.rawVirtualServers,
      virtualServers: state.executiveDashboard.virtualServers
    };
  }
)(ExecutiveDashboard);
