import React, { PureComponent } from 'react';
import { Cell, Pie, PieChart, ResponsiveContainer, Tooltip } from 'recharts';
import { connect } from 'react-redux';
import {
  LICENSABLE_FEATURE_DEFAULT_COLOR,
  LICENSABLE_FEATURE_DEFAULT_SECONDARY_COLOR,
  LICENSABLE_FEATURES_COLORS
} from '../../LicensableFeaturesGraph/LicensableFeaturesConstants';
import { Overflow } from '../../../../shared/components/layout';
import { ChartTooltip } from '../../ChartTooltip/ChartTooltip';
import { loadMssqlUsageThunk } from '../../../store/thunks';
import { OptionsList } from '../../OptionsTile/components/OptionsList/OptionsList';
import { Center } from '../../../../shared/components';
import { Loader } from '../../../../shared/components/loaders';

import './LicenseUsageTile.local.scss';

class MssqlLicenseUsageTile extends PureComponent {
  componentDidMount() {
    const { getLicenseUsage } = this.props;
    getLicenseUsage();
  }

  UNSAFE_componentWillReceiveProps(newProps) {
    if (newProps.selectedServer !== this.props.selectedServer) {
      const { getLicenseUsage } = this.props;
      setTimeout(() => {
        getLicenseUsage(newProps.selectedServer);
      });
    }
  }

  render() {
    const { mssqlLicenseUsage, isLoading } = this.props;

    if (isLoading) {
      return (
        <>
          <div className="exec-dashboard-graph-container license-usage-graph-container">
            <Center>
              <Loader>Loading License Usage Data...</Loader>
            </Center>
          </div>
        </>
      );
    }

    const licenseUsageData = this.mapLicenses(mssqlLicenseUsage);
    return (
      <div className="exec-dashboard-graph-container license-usage-graph-container">
        {licenseUsageData.length ? (
          <>
            <div className="chart-title">License Usage</div>
            <ResponsiveContainer height={380}>
              <PieChart>
                <Pie data={licenseUsageData} dataKey="value" cx="20%" cy="40%" innerRadius={65} outerRadius={130} blendStroke>
                  {licenseUsageData.map((entry, index) => (
                    <Cell key={`cell-${index}`} fill={entry.color} />
                  ))}
                </Pie>
                <Tooltip content={ChartTooltip} />
              </PieChart>
            </ResponsiveContainer>

            <div className="graph-legend-container">
              <Overflow>
                <OptionsList options={licenseUsageData} />
              </Overflow>
            </div>
          </>
        ) : (
          <Center>
            No Data Available
          </Center>
        )}
      </div>
    );
  }

  mapLicenses = (licenses) => {
    return (licenses || []).map((license, index) => {
      const licenseConfig = LICENSABLE_FEATURES_COLORS[index];
      return {
        ...license,
        value: license.count,
        name: license.license_type,
        label: license.license_type,
        color: licenseConfig ? licenseConfig.color : LICENSABLE_FEATURE_DEFAULT_COLOR,
        secondaryColor: licenseConfig ? licenseConfig.secondaryColor : LICENSABLE_FEATURE_DEFAULT_SECONDARY_COLOR,
      };
    });
  }
}

export const MssqlConnectedLicenseUsageTile = connect(
  state => {
    return {
      isLoading: state.executiveDashboard.flags.mssqlLicenseUsage.loading,
      mssqlLicenseUsage: state.executiveDashboard.mssqlLicenseUsage,
    };
  },
  dispatch => ({
    getLicenseUsage: (serverId) => dispatch(loadMssqlUsageThunk(serverId)),
  })
)(MssqlLicenseUsageTile);
